    column-span: all !important;
    display: block;
    margin-left: 30%;
    margin-right: -30%;
    line-height: 1.3em;
    font-style: italic;
    font-size: 19px;
}


.MsoTableGrid tr:nth-child(odd) td:first-child {
    text-align: right;
    direction: rtl;
}

